'use client';

import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Paper,
  IconButton,
  Menu,
  MenuItem,
  Fade,
  Button,
  Tabs,
  Tab,
  Chip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Card,
  CardContent,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  Divider,
  Alert
} from '@mui/material';
import {
  AccountCircle,
  Notifications,
  ExitToApp,
  People,
  PersonAdd,
  Map,
  AdminPanelSettings,
  AccountTree,
  Assignment,
  TransferWithinAStation,
  SwapHoriz,
  Business,
  Badge,
  LocationOn,
  Close
} from '@mui/icons-material';
import UserManagement from '../admin/UserManagement';
import MapUlbToDistrict from '../admin/MapUlbToDistrict';
import MapSuperAdmin from '../admin/MapSuperAdmin';
import MapLevelsToRoles from '../admin/MapLevelsToRoles';
import MapUser from '../admin/MapUser';
import TransferEmployee from '../admin/TransferEmployee';
import ChangePassword from '../auth/ChangePassword';

interface ULBMapping {
  ulbId: string;
  ulbName: string;
  roleType: string;
  district: string;
}

interface UserInfo {
  employeeId: string;
  employeeName: string;
  ulbMappings: ULBMapping[];
}

interface DashboardProps {
  userInfo: UserInfo;
  selectedULB: ULBMapping;
  onLogout: () => void;
}

const Dashboard: React.FC<DashboardProps> = ({ userInfo, selectedULB, onLogout }) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [showSwitchULB, setShowSwitchULB] = useState(false);
  const [selectedNewULB, setSelectedNewULB] = useState<string>('');

  const [currentView, setCurrentView] = useState<'employees' | 'map-ulb' | 'map-super-admin' | 'map-levels' | 'map-user' | 'transfer-employee' | 'change-password'>('employees');

  const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };



  const handleChangePassword = () => {
    setCurrentView('change-password');
    handleClose();
  };

  const handleBackToDashboard = () => {
    setCurrentView('employees');
  };

  const handleAdminNavigation = (event: React.SyntheticEvent, newValue: 'employees' | 'map-ulb' | 'map-super-admin' | 'map-levels' | 'map-user' | 'transfer-employee') => {
    setCurrentView(newValue);
  };

  const handleLogout = () => {
    handleClose();
    onLogout();
  };

  const handleSwitchULB = () => {
    if (!selectedNewULB) {
      return;
    }
    // In a real application, this would update the user's selected ULB
    // For now, we'll just close the dialog
    setShowSwitchULB(false);
    setSelectedNewULB('');
    // Reload the page or redirect to ULB selection
    window.location.reload();
  };

  const handleCloseSwitchULB = () => {
    setShowSwitchULB(false);
    setSelectedNewULB('');
  };

  return (
    <Box sx={{ minHeight: '100vh', bgcolor: '#f1f5f9', display: 'flex', flexDirection: 'column' }}>

      {/* Top Accessibility Bar */}
      <Box sx={{ bgcolor: '#1e293b', color: '#cbd5e1', py: 0.5, px: 3, display: 'flex', justifyContent: 'flex-end', borderBottom: '1px solid #334155' }}>
        <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
          <Typography variant="caption" sx={{ fontSize: '11px', mr: 2 }}>Skip to Main Content</Typography>
          {['A', 'A', '-', 'A', '+', 'En'].map((text, index) => (
            <Box
              key={index}
              component="button"
              sx={{
                border: '1px solid #475569',
                bgcolor: 'transparent',
                color: 'inherit',
                px: 1,
                fontSize: '10px',
                borderRadius: 1,
                cursor: 'pointer',
                '&:hover': { bgcolor: '#334155', color: 'white' },
                transition: 'all 0.2s'
              }}
            >
              {text}
            </Box>
          ))}
        </Box>
      </Box>

      {/* Main Header */}
      <Box sx={{ bgcolor: 'white', py: 2.5, borderBottom: '1px solid #e2e8f0', boxShadow: '0 4px 6px -1px rgba(0,0,0,0.05)', position: 'relative', zIndex: 10 }}>
        <Container maxWidth="xl">
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 3 }}>
              <Box
                sx={{
                  width: 85,
                  height: 85,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  p: 0.7,
                  borderRadius: 2,
                  border: '1px solid #e2e8f0',
                  boxShadow: '0 4px 6px rgba(0,0,0,0.05)'
                }}
              >
                <img src="/images/karnataka_emblem.png" alt="Karnataka Government Logo" style={{ width: '100%', height: '100%', objectFit: 'contain' }} />
              </Box>
              <Box>
                <Typography variant="h4" sx={{ fontWeight: 800, color: '#0f172a', lineHeight: 1.2, letterSpacing: '-0.5px' }}>
                  Directorate of Municipal Administration
                </Typography>
                <Typography variant="subtitle1" sx={{ color: '#002147', fontWeight: 700, letterSpacing: '1.5px', textTransform: 'uppercase', mt: 0.7 }}>
                  Government of Karnataka
                </Typography>
              </Box>
            </Box>

            <Box sx={{ display: { xs: 'none', md: 'block' } }}>
              <Paper elevation={0} sx={{ p: 1.5, borderRadius: 2, border: '1px solid #e2e8f0' }}>
                <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
                  <Typography variant="caption" sx={{ color: '#64748b', mb: 0.5, fontWeight: 600 }}>Helpline Number</Typography>
                  <Typography variant="h6" sx={{ fontWeight: 700, color: '#002147' }}>1800-425-1111</Typography>
                </Box>
              </Paper>
            </Box>
          </Box>
        </Container>
      </Box>

      {/* Navigation Bar */}
      <Box sx={{
        bgcolor: '#002147',
        py: 1.2,
        px: 3,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        background: 'linear-gradient(to right, #002147, #0f172a)'
      }}>
        <Container maxWidth="xl" sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', flex: 1 }}>
            <Typography variant="h6" sx={{ 
              color: 'white', 
              fontWeight: 600, 
              mr: 3,
              minWidth: 'fit-content'
            }}>
              Admin Panel
            </Typography>
            <Tabs
              value={['employees', 'map-ulb', 'map-super-admin', 'map-levels', 'map-user', 'transfer-employee'].includes(currentView) ? currentView : false}
              onChange={handleAdminNavigation}
              sx={{
                flex: 1,
                '& .MuiTabs-indicator': {
                  backgroundColor: '#60a5fa',
                  height: 3,
                  borderRadius: '1.5px 1.5px 0 0',
                  transition: 'all 0.3s ease-in-out'
                },
                '& .MuiTab-root': {
                  color: 'rgba(255,255,255,0.7)',
                  fontWeight: 500,
                  textTransform: 'none',
                  minHeight: 48,
                  fontSize: '0.9rem',
                  borderRadius: '8px 8px 0 0',
                  transition: 'all 0.2s ease-in-out',
                  '&.Mui-selected': {
                    color: 'white',
                    fontWeight: 600,
                    backgroundColor: 'rgba(96, 165, 250, 0.1)'
                  },
                  '&:hover': {
                    color: 'white',
                    backgroundColor: 'rgba(255,255,255,0.1)',
                    transform: 'translateY(-1px)'
                  },
                  '& .MuiTab-iconWrapper': {
                    marginBottom: '4px !important',
                    marginRight: '8px !important'
                  }
                },
                '& .MuiTabs-scrollButtons': {
                  color: 'rgba(255,255,255,0.7)',
                  '&:hover': {
                    color: 'white'
                  }
                }
              }}
              variant="scrollable"
              scrollButtons="auto"
              allowScrollButtonsMobile
            >
              <Tab 
                value="employees" 
                label="User Management" 
                icon={<PersonAdd sx={{ fontSize: '1.1rem' }} />}
                iconPosition="start"
                sx={{ minWidth: 140 }}
              />
              <Tab 
                value="map-ulb" 
                label="ULB Mapping" 
                icon={<Map sx={{ fontSize: '1.1rem' }} />}
                iconPosition="start"
                sx={{ minWidth: 120 }}
              />
              <Tab 
                value="map-super-admin" 
                label="Super Admin" 
                icon={<AdminPanelSettings sx={{ fontSize: '1.1rem' }} />}
                iconPosition="start"
                sx={{ minWidth: 130 }}
              />
              <Tab 
                value="map-levels" 
                label="Levels & Roles" 
                icon={<AccountTree sx={{ fontSize: '1.1rem' }} />}
                iconPosition="start"
                sx={{ minWidth: 140 }}
              />
              <Tab 
                value="map-user" 
                label="User Mapping" 
                icon={<Assignment sx={{ fontSize: '1.1rem' }} />}
                iconPosition="start"
                sx={{ minWidth: 130 }}
              />
              <Tab 
                value="transfer-employee" 
                label="Transfer Employee" 
                icon={<TransferWithinAStation sx={{ fontSize: '1.1rem' }} />}
                iconPosition="start"
                sx={{ minWidth: 150 }}
              />
            </Tabs>
          </Box>

          <Box sx={{ display: 'flex', gap: 3, alignItems: 'center', color: 'white' }}>
            {/* User and ULB Info */}
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Box sx={{ textAlign: 'right' }}>
                <Typography variant="body2" sx={{ fontWeight: 600, color: 'white' }}>
                  {userInfo.employeeName}
                </Typography>
                <Typography variant="caption" sx={{ color: 'rgba(255,255,255,0.8)' }}>
                  ID: {userInfo.employeeId}
                </Typography>
              </Box>
              
              <Box sx={{ 
                bgcolor: 'rgba(255,255,255,0.1)', 
                px: 2, 
                py: 1, 
                borderRadius: 2,
                border: '1px solid rgba(255,255,255,0.2)'
              }}>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 0.5 }}>
                  <Business sx={{ fontSize: '1rem' }} />
                  <Typography variant="body2" sx={{ fontWeight: 600 }}>
                    {selectedULB.ulbName}
                  </Typography>
                </Box>
                <Chip 
                  label={selectedULB.roleType}
                  size="small"
                  sx={{ 
                    bgcolor: 'rgba(255,255,255,0.2)',
                    color: 'white',
                    fontSize: '0.7rem',
                    height: 20
                  }}
                />
              </Box>
              
              {/* Switch ULB Button - Only show if user has multiple ULBs */}
              {userInfo.ulbMappings.length > 1 && (
                <Button
                  variant="outlined"
                  size="small"
                  startIcon={<SwapHoriz />}
                  onClick={() => setShowSwitchULB(true)}
                  sx={{
                    color: 'white',
                    borderColor: 'rgba(255,255,255,0.3)',
                    textTransform: 'none',
                    '&:hover': {
                      borderColor: 'white',
                      bgcolor: 'rgba(255,255,255,0.1)'
                    }
                  }}
                >
                  Switch ULB
                </Button>
              )}
            </Box>
            
            <IconButton 
              size="medium" 
              sx={{ 
                color: 'white', 
                '&:hover': { bgcolor: 'rgba(255,255,255,0.1)' },
                borderRadius: 2
              }}
            >
              <Notifications />
            </IconButton>
            <Box>
              <IconButton
                size="medium"
                onClick={handleMenu}
                sx={{
                  color: 'white',
                  '&:hover': { bgcolor: 'rgba(255,255,255,0.1)' },
                  border: '1px solid rgba(255,255,255,0.2)',
                  borderRadius: 2,
                  p: 1
                }}
              >
                <AccountCircle sx={{ fontSize: '1.8rem' }} />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
                PaperProps={{
                  elevation: 3,
                  sx: {
                    overflow: 'visible',
                    filter: 'drop-shadow(0px 4px 12px rgba(0,0,0,0.2))',
                    mt: 1.5,
                    borderRadius: 2,
                    minWidth: 220,
                    '& .MuiAvatar-root': {
                      width: 32,
                      height: 32,
                      ml: -0.5,
                      mr: 1,
                    },
                    '&:before': {
                      content: '""',
                      display: 'block',
                      position: 'absolute',
                      top: 0,
                      right: 14,
                      width: 10,
                      height: 10,
                      bgcolor: 'background.paper',
                      transform: 'translateY(-50%) rotate(45deg)',
                      zIndex: 0,
                    },
                  },
                }}
              >
                <MenuItem 
                  onClick={handleClose}
                  sx={{ py: 1.2 }}
                >
                  Profile
                </MenuItem>
                <MenuItem 
                  onClick={handleChangePassword}
                  sx={{ 
                    py: 1.2,
                    '&:hover': { bgcolor: '#f1f5f9' }
                  }}
                >
                  Change Password
                </MenuItem>
                <MenuItem 
                  onClick={handleLogout} 
                  sx={{ 
                    color: 'error.main', 
                    py: 1.2,
                    '&:hover': { bgcolor: '#fef2f2' }
                  }}
                >
                  <ExitToApp sx={{ mr: 1 }} />
                  Logout
                </MenuItem>
              </Menu>
            </Box>
          </Box>
        </Container>
      </Box>

      {/* Main Content */}
      {currentView === 'change-password' ? (
        <ChangePassword onBack={handleBackToDashboard} />
      ) : (
        <Container maxWidth="xl" sx={{ mt: 4, mb: 4, flexGrow: 1 }}>
          <Fade in timeout={800} key={currentView}>
            <Box>
              {currentView === 'employees' ? <UserManagement /> :
                currentView === 'map-ulb' ? <MapUlbToDistrict /> :
                  currentView === 'map-super-admin' ? <MapSuperAdmin /> :
                    currentView === 'map-levels' ? <MapLevelsToRoles /> :
                      currentView === 'map-user' ? <MapUser /> :
                        <TransferEmployee />}
            </Box>
          </Fade>
        </Container>
      )}

      {/* Switch ULB Dialog */}
      <Dialog
        open={showSwitchULB}
        onClose={handleCloseSwitchULB}
        maxWidth="sm"
        fullWidth
        PaperProps={{
          sx: {
            borderRadius: 3,
            boxShadow: '0 25px 50px -12px rgba(0, 0, 0, 0.25)'
          }
        }}
      >
        <DialogTitle sx={{ 
          display: 'flex', 
          alignItems: 'center', 
          justifyContent: 'space-between',
          pb: 2
        }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <SwapHoriz sx={{ color: '#1976d2' }} />
            <Typography variant="h6" fontWeight={600}>
              Switch ULB
            </Typography>
          </Box>
          <IconButton onClick={handleCloseSwitchULB} size="small">
            <Close />
          </IconButton>
        </DialogTitle>
        
        <DialogContent sx={{ pb: 2 }}>
          <Alert severity="info" sx={{ mb: 3 }}>
            Select a different ULB to continue your session. This will reload the application with the new ULB context.
          </Alert>
          
          <FormControl component="fieldset" sx={{ width: '100%' }}>
            <RadioGroup
              value={selectedNewULB}
              onChange={(e) => setSelectedNewULB(e.target.value)}
              sx={{ gap: 1 }}
            >
              {userInfo.ulbMappings
                .filter(ulb => ulb.ulbId !== selectedULB.ulbId)
                .map((ulb) => (
                <Card 
                  key={ulb.ulbId}
                  variant="outlined"
                  sx={{ 
                    cursor: 'pointer',
                    transition: 'all 0.2s',
                    border: selectedNewULB === ulb.ulbId ? '2px solid #1976d2' : '1px solid #e0e0e0',
                    backgroundColor: selectedNewULB === ulb.ulbId ? '#f3f7ff' : 'white',
                    '&:hover': {
                      backgroundColor: '#f8f9fa',
                      borderColor: '#1976d2'
                    }
                  }}
                  onClick={() => setSelectedNewULB(ulb.ulbId)}
                >
                  <CardContent sx={{ py: 2 }}>
                    <Box display="flex" alignItems="center">
                      <FormControlLabel
                        value={ulb.ulbId}
                        control={<Radio sx={{ color: '#1976d2' }} />}
                        label=""
                        sx={{ m: 0, mr: 2 }}
                      />
                      
                      <Box flex={1}>
                        <Typography variant="h6" fontWeight={600} color="#1976d2">
                          {ulb.ulbName}
                        </Typography>
                        <Box display="flex" alignItems="center" gap={2} mt={1}>
                          <Chip
                            label={ulb.roleType}
                            size="small"
                            color="primary"
                            variant="outlined"
                            icon={<Badge />}
                          />
                          <Box display="flex" alignItems="center" gap={0.5}>
                            <LocationOn sx={{ fontSize: 16, color: 'text.secondary' }} />
                            <Typography variant="body2" color="text.secondary">
                              {ulb.district}
                            </Typography>
                          </Box>
                        </Box>
                      </Box>
                    </Box>
                  </CardContent>
                </Card>
              ))}
            </RadioGroup>
          </FormControl>
        </DialogContent>
        
        <DialogActions sx={{ p: 3, pt: 1 }}>
          <Button onClick={handleCloseSwitchULB}>
            Cancel
          </Button>
          <Button 
            onClick={handleSwitchULB}
            variant="contained"
            disabled={!selectedNewULB}
            sx={{
              px: 3,
              background: 'linear-gradient(45deg, #1976d2 30%, #42a5f5 90%)',
              '&:hover': {
                background: 'linear-gradient(45deg, #1565c0 30%, #1976d2 90%)',
              }
            }}
          >
            Switch ULB
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Dashboard;